.critter-item {
  width: 100%;
  padding: 0;
  border: none;
  transition: 0.3s;
}

.critter-item__image {
  width: calc(100% - 2px);
  transition: 0.3s;
  height: 100%;
  object-fit: contain;
}

.critter-item.critter-item--museum-donated {
  background: yellow;
}

.critter-item.critter-item--critter-registered {
  background: blue;
}

.critter-item.critter-item--both {
  background: green;
}

.critter-item.critter-item--grayed-out .critter-item__image {
  filter: grayscale(100%);
}

.critter-item:hover,
.critter-item .critter-item__image:hover {
  background: red;
}
